@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
.overl {
  position: absolute;
  top: 0;
  left: 0;
}

.o-active {
  width: 100vw;
  height: 100vh;
  background: white;
  pointer-events: all;
  opacity: 0.5;
  z-index: 1;
}

button {
  color: rgb(77, 77, 77);
  cursor: pointer;
}

.button {
  border-radius: 32px;
  font-weight: 700;
  font-size: 1rem;
  background: radial-gradient(white 10%, 70%, #7abfff);
  width: 120px;
  height: 50px;
  transition: transform 1s;
  padding: 10px;
  text-align: center;
  border: 2px outset gray;
  box-shadow: 1px 1px 2px 1px black;
}
.button:hover {
  color: black;
  transform: scale(1.05);
  background-color: #56adff;
}
.button:active {
  color: #8b0000;
  background: white;
  transform: scale(1.3);
}

#show1, #show2 {
  background: radial-gradient(white 10%, 70%, gray);
  width: auto;
  height: auto;
  opacity: 1;
  border: 0px;
}

.btnX {
  font-size: 1.5rem;
  outline: none;
  border: none;
  background: none;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
}

.reset {
  visibility: hidden;
  -webkit-transform: scale(0);
  transform: scale(0);
  border-radius: 32px;
  font-weight: 700;
  font-size: 1rem;
  background: radial-gradient(white 10%, 70%, #7abfff);
  width: auto;
  height: 50px;
  transition: transform 1s;
  padding-block: auto;
  box-shadow: 1px 1px 2px 1px gray;
  margin-left: 3vw;
  border: 3px outset gray;
}

.r-active {
  visibility: visible;
  transform: scale(1);
}
.r-active:hover {
  color: black;
  transform: scale(1.05);
}
.r-active:active {
  color: #8b0000;
  background: white;
  transform: scale(1.1);
}

.popup {
  visibility: hidden;
  width: 40vw;
  background-color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  -webkit-box-shadow: 0px 0px 7px 1px gray;
  box-shadow: 0px 0px 7px 1px gray;
  z-index: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: left;
  -ms-flex-align: left;
  align-items: left;
  line-height: 1.6;
  padding: 2% 5% 2% 5%;
}
.popup > h2 {
  font-size: 2rem;
}
.popup > p {
  font-size: 1rem;
}
.popup > p > span {
  color: #8b0000;
  font-size: 1.5rem;
  font-weight: 700;
}

.active {
  visibility: visible !important;
  -webkit-transform: translate(-50%, -50%) scale(1);
  transform: translate(-50%, -50%) scale(1);
}

@keyframes spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes op {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.Ball {
  animation-name: spinning;
  border-radius: 80%;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in, ease-out;
  background-image: radial-gradient(#fff700, #ff0000, #afffb6);
}

.BallRunning {
  animation-name: spinning, op;
  border-radius: 80%;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  background-image: radial-gradient(#fff700, #ff0000, #afffb6);
}

.Ball:hover {
  cursor: pointer;
  animation-name: none;
  transform: scale(1.8);
}

.introBall2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.part2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-align: center;
  background-image: radial-gradient(#ff8765, #ffffff, #97fdff);
  height: auto;
}

.boardBall2 {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-top: 3%;
  width: 90%;
  height: 60%;
  font-family: Webdings;
  font-size: 2vw;
  margin-bottom: 5%;
}

.Air {
  background-color: #afffb6;
}

.y {
  color: red;
  background-color: #fff347;
}

.Wall {
  background-color: #ce9980;
}

html {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  scroll-behavior: smooth;
}

.hr {
  margin-block: 10px;
  width: 80%;
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid gray;
}

#intro {
  display: flex;
  background-color: rgb(230, 249, 255);
  background-image: linear-gradient(rgb(147, 185, 255), rgb(230, 249, 255) 5%, 95%, rgb(147, 185, 255));
  align-items: center;
  margin-top: 1%;
  margin-inline: 5%;
  box-shadow: inset 0px 0px 5px 1px rgb(165, 165, 165), 0px 0px 5px 1px rgb(165, 165, 165);
  border: 1px solid gray;
  border-radius: 15px;
}
#intro > .center {
  display: flex;
  flex-direction: column;
  text-align: justify;
  margin-inline: 3%;
}
#intro > .center h1 {
  text-align: center;
}
#intro > .center p {
  font-style: italic;
  margin-inline: 3%;
  margin-bottom: 3%;
}

.img {
  box-shadow: inset 0px 0px 5px 1px rgb(165, 165, 165), 0px 0px 5px 1px rgb(165, 165, 165);
  border-radius: 10px;
  cursor: pointer;
}

.showAndHideMenu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 3%;
}
.showAndHideMenu p, .showAndHideMenu button {
  margin-inline: 10px;
}

body main {
  display: flex;
  flex-direction: column;
  justify-items: center;
}

.part1 {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  background-image: linear-gradient(white, lightblue, white);
  grid-gap: 10px 10px;
}
.part1.img-container {
  margin-top: 1%;
  width: 40%;
  height: 40%;
  display: flex;
  justify-content: center;
}
.part1 > .content-container {
  margin-inline: 1vw;
  width: 40%;
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.part1 > .content-container > p {
  font-size: 1rem;
  margin-bottom: 2vh;
}

.part2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: linear-gradient(white, lightgreen, white);
}
.part2 h2 {
  text-align: center;
}
.part2 p {
  text-align: center;
  margin-inline: 10%;
  text-justify: distribute;
}